import { SectionsHeader } from "../sectionsHeader/sectionsHeader"
import { AboutMeContainer, PictureContainer, ResumeContainer, TextContainer } from "./styles"

export const AboutMe = () => {
    return(
        <div style={{backgroundColor: '#c5c5c5'}}>
            <AboutMeContainer data-aos='fade-down'>
                <SectionsHeader h1={'Sobre Mim'} h2={''}/>
                <TextContainer>
                    <PictureContainer/>
                    <ResumeContainer>
                        <p>Tenho 26 anos, nascido em Mogi das Cruzes, SP. Possuo formao superior na rea de Geologia pela Universidade Federal de Mato Grosso e desde de Janeiro de 2022 venho empreendendo no desafio da transio de carreira para rea de <span>Desenvolvimento Web Front-end</span>, estudando as principais tecnologias do ramo e em busca do primeiro emprego como desenvolvedor.</p>
                        <p>Sou uma pessoa <span>responsvel</span>, <span>dedicada</span> ao meu trabalho, muito cuidadosa com <span>prazos</span>, relacionamentos profissionais e <span>atenta aos detalhes</span> do meu trabalho. Estou sempre disposto a adquirir novos conhecimentos que possam engrandecer meu ofcio e aprimorar meus resultados e carreira. </p>
                        <p>Sou predominantemente introspectivo, porm muito <span>comunicativo</span> ao trabalhar em equipes, apresentar produtos, expor ideias e quando necessrio. Gosto de <span>ajudar meus colegas</span> a aprender novas tcnicas/conceitos, lido muito bem com <span>feedbacks</span> dos meus pares e busco sempre atender s expectativas. </p>
                    </ResumeContainer>
                </TextContainer>
            </AboutMeContainer>
        </div>
    )
}